
import Actionsheet from 'vux/src/components/actionsheet/index.vue'


export default {
components: {
Actionsheet
},
data () {
return {
show: false,
menus: {}
}
},
created () {
window.$event.on('app:actionsheet', ({ show, menus }) => {
this.show = !!show
this.menus = menus
})
},
methods: {
click (key) {
window.$event.emit('layout-actionsheet:click', key)
},
close () {
window.$event.emit('layout-actionsheet:close')
}
}
}



