
import Alert from 'vux/src/components/alert/index.vue'


export default {
components: {
Alert
},
data () {
return {
show: false,
title: '',
content: ''
}
},
created () {
window.$event.on('app:alert', ({ show, title = '', content = '' }) => {
this.show = !!show
this.title = `${title}`
this.content = `${content}`
})
},
methods: {
hide () {
window.$event.emit('layout-alert:click')
}
}
}



