
import XHeader from 'vux/src/components/x-header/index.vue'

import grid from '@/img/grid.svg'
import list from '@/img/list.svg'
import plus from '@/img/plus.svg'
import i18nBase from './i18n-base'
import i18nPage from './i18n-page'

export default {
components: {
XHeader,
i18nBase,
i18nPage
},
data () {
return {
grid,
list,
plus,
style: null
}
},
computed: {
displayHeader () {
switch (this.$route.name) {
case 'index':
return false
}
return true
},
showBack () {
switch (this.$route.name) {
case 'checkcode':
case 'home':
case 'name':
case 'person':
case 'wish':
case 'wish-add':
case 'wish-edit':
return false
}
return true
},
backText () {
return this.$t('layout-navbar@back')
},
preventGoBack () {
switch (this.$route.name) {
case 'wish-edit':
return true
}
return false
},
showMore () {
switch (this.$route.name) {
case 'photo':
case 'wish-detail':
return true
}
return false
},
displayCancel () {
switch (this.$route.name) {
case 'checkcode':
case 'name':
case 'wish-add':
case 'wish-edit':
return true
}
return false
},
displaySave () {
switch (this.$route.name) {
case 'checkcode':
case 'name':
case 'wish-edit':
return true
}
return false
},
displayConfirm () {
return false
},
displayCreate () {
switch (this.$route.name) {
case 'wish-add':
return true
}
return false
},
displayGridOrList () {
switch (this.$route.name) {
case 'home':
return true
}
return false
},
displayPlus () {
switch (this.$route.name) {
case 'wish':
return true
}
return false
}
},
methods: {
onClickTitle () {
window.$event.emit('layout-navbar:click', 'title')
},
onClickBack () {
window.$event.emit('layout-navbar:click', 'back')
},
onClickMore () {
window.$event.emit('layout-navbar:click', 'more')
},
onCancel () {
window.$event.emit('layout-navbar:click', 'cancel')
},
onSave () {
window.$event.emit('layout-navbar:click', 'save')
},
onConfirm () {
window.$event.emit('layout-navbar:click', 'confirm')
},
onCreate () {
window.$event.emit('layout-navbar:click', 'create')
},
async onGridOrList () {
const style = this.$storage.config['home:style']
let value
if (style === 'list') {
value = 'grid'
}
if (style === 'grid') {
value = 'list'
}
await this.$forage({ type: 'set', key: 'config@home:style', value })
},
onPlus () {
window.$event.emit('layout-navbar:click', 'plus')
}
},
created () {
window.$event.on('layout-navbar:style', (callback) => {
if (callback) {
this.style = callback()
} else {
this.style = null
}
})
}
}



